/* === Variables === */
:root {
  --color-primary: #1fb3c7;
  --color-primary-hover: #06b6d4;
  --color-secondary: #2d3748;
  --color-secondary-hover: #374151;
  /* Color background for the whole body besides nav color*/
  --color-background: rgb(0, 0, 0);
  --color-text: #e2e8f0;
  --color-border: #374151;
  --color-error: #f87171;

  --font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

  --spacing-small: 0.5rem;
  --spacing-medium: 1rem;
  --spacing-large: 2rem;

  --border-radius: 6px;
  --box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Remove window slider.*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* === Responsive Design === */
@media (max-width: 768px) {
  .form-container {
    padding: var(--spacing-medium);
  }

  .form input {
    max-width: 100%;
  }
}

/* Global Style for the whole site*/
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: var(--color-background);
  color: var(--color-text);
  font-family: var(--font-family);
 
}


/* Utility class */

text.center {
  text-align: center;
}
 .flex {
  display: flex;}

.flex.column {
  flex-direction: column;}

  .flex-center {
    justify-content: center;
    align-items: center;
  }

  .mt-medium {
    margin-top: var(--spacing-medium);
  }

  .mb-medium {
  margin-bottom: var(--spacing-medium);
}

.p-medium {
  padding: var(--spacing-medium);
}

.rounded {
  border-radius: var(--border-radius);
}

.shadow {
  box-shadow: var(--box-shadow);
}

/* Form Containers for Log in and Create Account*/

.form-container {
  background-color: var(--color-secondary);
  color: var(--color-text);
  max-width: 400px;
  width: 100%;
  margin: auto;
  padding: var(--spacing-large);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.form-container h2 {
color: var(--color-primary);  
margin-bottom: var(--spacing-medium);
}

form {
  display: flex;
  flex-direction: column;
}

.form input {
    width: 100%;
    max-width: 300px;
    padding: var(--spacing-small);
    font-size: 1rem;
    border: none;
    border-radius: var(--border-radius);
    background-color: var(--color-secondary-hover);
    color: var(--color-text);
}
.form input:focus {
  outline: none;
  background-color: var(--color-primary-hover);
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.5);
}

.form button {
  padding: var(--spacing-small);
  background-color: var(--color-primary);
  border: none;
  border-radius: var(--border-radius);
  color: #111827;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.form button:hover {
  background-color: var(--color-primary-hover);
}
