/* Disable scrolling for static pages */
html.static-page, body.static-page {
  overflow: hidden; 
  height: 100%;  
  margin: 0; 
  padding: 0; 
}

/* Ensure the app container fills the viewport */
.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; 
  height: 100vh; 
  margin: 0; 
}

main.page-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; 
}

.button-wrapper {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}


