.register-container {
  text-align: center;
  padding: 2rem;
  background-color: #2d3748;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: #e2e8f0;
  max-width: 400px;
  width: 100%;
  margin: auto;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
}

.register-form input {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
  padding: 0.75rem;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  background-color: #3b4452;
  color: #f9fafb;
}

.register-form input:focus {
  outline: none;
  background-color: #4b5563;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.5);
}

.register-form button {
  padding: 0.75rem;
  background-color: #e5ecf3;
  border: none;
  border-radius: 6px;
  color: #141414;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.lootdrop-button1 {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: none;
  border: none;
  color: rgb(246, 246, 246);
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  transition: color 0.2s ease, transform 0.2s ease;
}

.lootdrop-button1:hover {
  color: var(--color-primary-hover);
  transform: scale(1.1);
}

.close-button1 {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s ease, transform 0.2s ease;
}

.close-button1:hover {
  color: var(--color-primary-hover);
  transform: scale(1.1);
}